.map-h2 {
    text-transform: uppercase;
    font-size: 1rem;
    padding: 20px;
    padding-left: 10px;
    text-align: center;
  }
  
  .google-map {
    width: 100%;
    height: 60vh;
  }
  
  .pin {
    background-color: green;
    position: absolute;
    color: blue;
  }
  
  .pin-icon {
    width: 30px;
    height: inherit;
    position: absolute;
    top: -25px;
    left: -15px;        
    }
  
  .pin-text {
    position: absolute;
    width: auto;
    top: -38px;
    left: 15px;
    font-size: 1.8em;
  }
  
  @media screen and (min-width: 799px) {
    .google-map {
      height: 80vh;
    }
  
    .map-h2 {
      font-size: 1.3rem;
      font-weight: 400;
    }
  
    .pin {
      width: 15vw;
    }
  
    .pin-icon {
      font-size: 10vw;
    }
  }
  